@import '../settings/colors';
@import '../settings/spacing';
@import '../settings/type';
@import '../settings/z-index';
@import '../utils/breakpoints';

.page-strip {
    width: 100%;
    position: relative;
    padding: ($space * 4) 0;
    z-index: $z-base;

    @include breakpoint(tablet) {
        padding: ($space * 3) 0;
    }

    &--reduced-top-padding {
        padding-top: $space * 2;

        @include breakpoint(tablet) {
            padding-top: $space;
        }
    }

    &--reduced-top-padding-mobile {
        @media only screen and (max-width: 759px) {
            padding-top: $space * 1.5;
        }
    }

    &--reduced-bottom-padding {
        padding-bottom: $space * 2;

        @include breakpoint(tablet) {
            padding-bottom: $space * 4;
        }
    }

    &--reduced-bottom-padding-mobile {
        @media only screen and (max-width: 759px) {
            padding-bottom: $space * 1;
        }
    }

    &--extended-top-padding {
        padding-top: $space * 5;

        @include breakpoint(tablet) {
            padding-top: $space * 8;
        }
    }

    &--extended-bottom-padding {
        padding-bottom: $space * 6.5;

        @include breakpoint(tablet) {
            padding-bottom: $space * 11;
        }
    }

    &--normal-top-padding {
        padding-top: $space * 4;

        @include breakpoint(tablet) {
            padding-top: $space * 5;
        }
    }

    &--no-bottom-padding {
        padding-bottom: 0;

        @include breakpoint(tablet) {
            padding-bottom: 0;
        }
    }

    &--no-top-padding {
        padding-top: 0;

        @include breakpoint(tablet) {
            padding-top: 0;
        }
    }

    &--has-section-divide {
        margin-top: 80px;
    }

    &--primary {
        background-color: $primary;
    }

    &--secondary {
        background-color: $secondary;
    }

    &--light {
        background-color: $grey-light;
    }

    &--with-background {
        background-repeat: no-repeat;
        background-size: cover;
        background-image: url('../img/placeholders/half-background-mobile.png');

        @include breakpoint(tablet) {
            background-image: url('../img/placeholders/half-background.png');
        }
    }

    &--on-top {
        z-index: $z-overlay;
    }

    &__title-container {
        text-align: center;
    }

    &__title {
        color: $secondary;

        &--white {
            color: $white;
        }

        &--big-margin-bottom {
            margin-bottom: $space * 5.5;

            @include breakpoint(tablet) {
                margin-bottom: $space * 5;
            }
        }

        &--big-mobile {
            font-size: 35px;
        }
    }

    &__text {
        color: $secondary;
        line-height: $line-height-big;
        margin-bottom: $space * 3;
        font-size: $font-size-desktop;

        &:last-of-type {
            margin-bottom: $space * 4;
        }

        &--white {
            color: $white;
        }
    }
}
