//@import '../settings/breakpoints';
@import '../settings/colors';

.arrow {
    background: none;

    &:hover {
        .arrow__icon {
            transform: scale(1.1);
        }
    }

    &__icon {
        display: block;
        color: $strongblue;
        font-size: 23px;
        transition: $easing-standard;

        &--light {
            color: $lightblue;
        }
    }
}
