@import '../settings/animations';
@import '../settings/breakpoints';
@import '../settings/colors';
@import '../settings/fonts';
@import '../settings/shadows';
@import '../settings/spacing';
@import '../settings/type';
@import '../utils/breakpoints';
@import '../utils/mixins';

.menu {
    position: relative;
    display: flex;
    justify-content: space-between;
    padding: 26px 0;

    @include breakpoint(tablet) {
        padding: 50px 0;
    }

    &__brand {
        display: flex;
        flex-direction: column;
        justify-content: center;

        &:hover {
            .menu__logo {
                transform: scale(1.1);
            }
        }
    }

    &__logo {
        max-width: 140px;
        transition: $easing-standard;

        @include breakpoint(tablet) {
            max-width: 100%;
        }
    }

    &__body {
        position: absolute;
        bottom: 100%;
        left: -10px;
        width: calc(100% + 20px);
        height: auto;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        transition: all 0.2s cubic-bezier(0.25, 0.1, 0.25, 1);

        &.active {
            padding-top: $space * 3;
            bottom: auto;
            top: 100%;
            background-color: $secondary;
        }

        @include breakpoint(tablet) {
            position: static;
            flex-direction: row;
            top: auto;
            bottom: auto;
            left: auto;
            height: auto;
            background: transparent;
            width: 100%;
            transition: none;
            overflow: initial;
            align-items: flex-end;
        }
    }

    &__list {
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        width: 100%;
        margin-bottom: 0;
        transition: all 0.2s cubic-bezier(0.25, 0.1, 0.25, 1);
        transition-delay: 0.2s;
        transition-duration: 0.3s;
        padding: 0 22px;
        align-items: flex-start;

        @include breakpoint(tablet) {
            position: static;
            flex-direction: row;
            margin-top: 0;
            top: auto;
            left: auto;
            max-width: none;
            padding: 0;
            width: 450px;
            align-items: center;
        }
    }

    &__item {
        margin-bottom: $space * 5;

        @include breakpoint(tablet) {
            margin-bottom: initial;
        }

        &--full-mobile {
            margin-top: 180px;
            width: 100%;
            margin-bottom: $space * 4;

            @include breakpoint(tablet) {
                margin-top: initial;
                margin-bottom: initial;
                width: initial;
            }
        }
    }

    &__link {
        position: relative;
        text-transform: uppercase;
        display: inline-block;
        color: $white;
        letter-spacing: $letter-spacing-regular;
        text-shadow: $shadow-standard;
        font-family: $font-heading;
        font-size: 20px;

        @include breakpoint(tablet) {
            font-size: $font-size-mobile;
        }

        &:after {
            @include center-h;

            position: absolute;
            display: block;
            content: '';
            bottom: -5px;
            width: 0;
            height: 3px;
            background-color: $primary;
            transition: $easing-standard;
        }

        &:hover {
            &:after {
                width: 100%;
            }
        }
    }

    &__button {
        @extend .button;
        @extend .button--primary;

        max-width: none;
        width: 100%;

        @include breakpoint(tablet) {
            width: auto;
        }
    }

    &__burger {
        position: relative;
        border: none;
        vertical-align: middle;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        transition: all 0.2s cubic-bezier(0.25, 0.1, 0.25, 1);
        transition-duration: 0.3s;
        padding: 6px 0 0;
        width: 23px;
        height: 26px;
        background-color: transparent;
        margin-right: 13px;
        cursor: pointer;

        span {
            transition: all 0.2s cubic-bezier(0.25, 0.1, 0.25, 1);
            background-color: $white;
            display: block;
            width: 23px;
            height: 3px;
            border-radius: 5px;
        }

        &:active,
        &:focus {
            span {
                background-color: $white;
            }
        }

        @media (min-width: $tablet) {
            display: none;
        }

        &.active {
            height: 22px;

            span {
                background-color: $white;
                width: 24px;

                &:first-child {
                    transform: rotate(45deg);
                }

                &:nth-child(2) {
                    transform: scale(0);
                }

                &:last-child {
                    transform-origin: left;
                    transform: translate(3px, -4px) rotate(-45deg)
                }
            }
        }
    }

    &__footer {
        display: flex;
        flex-direction: column;
        align-items: center;

        @include breakpoint(tablet) {
            display: none;
        }
    }

    &__secondary-item {
        margin-bottom: $space * 3;
    }

    &__secondary-logo {
        max-width: 150px;
    }

    &__copyright {
        font-size: $font-size-decreased;
        color: $white;
    }
}
