@import '../../settings/spacing';
@import '../../utils/breakpoints';

.slider {
    &-cards {
        margin-top: $space * 2;

        @include breakpoint(tablet) {
            margin-top: 52px;
        }

        @include breakpoint(desktop) {
            left: calc((100% - 1120px) / 2);
        }

        .slick-track {
            padding: $space * 1 0;

            @include breakpoint(tablet) {
                padding: $space * 2 0;
            }
        }

        .slick-slide {
            margin: 0 $space 0;
        }

        &__arrows {
            display: flex;
            justify-content: flex-end;

            @include breakpoint(tablet) {
                position: absolute;
                top: 10px;
                right: 0;
            }
        }

        &-container {
            overflow: hidden;
        }
    }
}
