/* stylelint-disable */
@font-face {
    font-family: 'icomoon';
    src:  url('../fonts/icomoon.eot?la58dc');
    src:  url('../fonts/icomoon.eot?la58dc#iefix') format('embedded-opentype'),
    url('../fonts/icomoon.woff2?la58dc') format('woff2'),
    url('../fonts/icomoon.ttf?la58dc') format('truetype'),
    url('../fonts/icomoon.woff?la58dc') format('woff'),
    url('../fonts/icomoon.svg?la58dc#icomoon') format('svg');
    font-weight: normal;
    font-style: normal;
    font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'icomoon' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.icon-minus:before {
    content: "\e902";
}
.icon-arrow-left:before {
    content: "\e900";
}
.icon-arrow-right:before {
    content: "\e901";
}
.icon-tick:before {
    content: "\e903";
}
.icon-x:before {
    content: "\e904";
}
/* stylelint-enable */