@import '../settings/animations';
@import '../settings/colors';
@import '../settings/fonts';
@import '../settings/shadows';
@import '../settings/spacing';
@import '../settings/type';
@import '../utils/mixins';
@import '../utils/breakpoints';

.footer {
    display: block;
    background-color: $secondary;
    padding: $space * 4 0 53px;

    @include breakpoint(tablet) {
        padding: $space * 3 0;
    }

    &__brand {
        margin-bottom: $space * 2;

        @include breakpoint(tablet) {
            margin-bottom: initial;
        }

        &:hover {
            .footer__logo {
                transform: scale(1.1);
            }
        }
    }

    &__logo {
        transition: $easing-standard;
        max-width: 150px;

        @include breakpoint(tablet) {
            max-width: initial;
        }
    }

    &__body {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-direction: column;

        @include breakpoint(tablet) {
            flex-direction: row;
        }
    }

    &__list {
        text-align: center;
        
        @include breakpoint(tablet) {
            text-align: left;
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 51%;
        }
    }

    &__link {
        position: relative;
        text-transform: uppercase;
        display: inline-block;
        color: $white;
        font-size: $font-size-mobile;
        letter-spacing: $letter-spacing-regular;
        font-family: $font-heading;

        &:after {
            @include center-h;

            position: absolute;
            display: block;
            content: '';
            bottom: -5px;
            width: 0;
            height: 3px;
            background-color: $primary;
            transition: $easing-standard;
        }

        &:hover {
            &:after {
                width: 100%;
            }
        }
    }

    &__copyright {
        color: $white;
        font-size: $font-size-decreased;
        margin-top: $space * 2.5;

        @include breakpoint(tablet) {
            font-size: $font-size-desktop;
            margin-top: initial;
        }
    }

    &__item {
        display: inline-block;

        @include breakpoint(tablet) {
            display: initial;
        }
    }
}
