@import '../settings/colors';
@import '../settings/fonts';
@import '../settings/spacing';
@import '../settings/shadows';
@import '../settings/type';
@import '../utils/breakpoints';

.table {
    $self: &;

    &__icon {
        font-size: 15px;
        color: $grey-mid;

        &--small {
            font-size: 5px;
        }

        &--big {
            font-size: 16px;
        }

        &--primary {
            color: $primary;
        }
    }

    &-container {
        padding-bottom: $space;
        margin: 25px 0 0;

        @include breakpoint(tablet) {
            margin: 69px 0 50px;
            background: $white;
            box-shadow: $shadow-soft;
            border-radius: $space;
            overflow: hidden;
        }
    }

    &__th {
        padding-top: 27px;
        padding-bottom: $space * 2;
        display: none;

        @include breakpoint(tablet) {
            display: initial;
        }
    }

    &__th,
    &__cell {
        text-align: center;
    }

    &__row {
        background: $white;
        box-shadow: $shadow-soft;
        border-radius: $space;
        overflow: hidden;
        margin-bottom: 20px;

        &:last-child {
            margin-bottom: 0;
        }

        @include breakpoint(tablet) {
            background: initial;
            box-shadow: none;
            border-radius: 0;
            overflow: initial;
            margin-bottom: initial;
        }

        &:nth-child(odd) {
            @include breakpoint(tablet) {
                background: rgba($lightblue, 0.1);
            }
        }

        #{$self}__cell {
            &:nth-child(even) {
                background: rgba($lightblue, 0.1);

                @include breakpoint(tablet) {
                    background: initial;
                }
            }
        }
    }

    &__head {
        #{$self}__row {
            &:nth-child(odd) {
                background: $white;
            }
        }
    }

    &__cell {
        font-family: $font-heading;
        color: $secondary;
        font-size: $font-size-increased;
        padding: 15px 20px !important;
        display: flex !important;
        justify-content: space-between;
        align-items: center;

        @include breakpoint(tablet) {
            text-align: center !important;
            display: table-cell !important;
        }

        &.table__text {
            padding: 25px 20px;

            @include breakpoint(tablet) {
                text-align: left !important;
                padding: initial;
            }

            .tablesaw-cell-label {
                display: none;
            }
        }
    }

    &__text {
        min-width: 240px;
        text-align: left;
        padding-left: $space * 2;
    }
}
