@import '../settings/colors';
@import '../settings/fonts';
@import '../settings/spacing';
@import '../settings/type';
@import '../utils/breakpoints';

.large-banner {
    position: relative;
    min-height: 500px;

    @include breakpoint(tablet) {
        min-height: 700px;
    }

    &:before,
    &:after {
        content: "";
        display: table;
    }

    &:after {
        clear: both;
    }

    &__image {
        display: block;
        height: 100%;
        left: 0;
        overflow: hidden;
        position: absolute;
        top: 0;
        width: 100%;
        z-index: -1;

        img {
            bottom: -9999px;
            display: block;
            left: -9999px;
            margin: auto;
            min-height: 100%;
            min-width: 100%;
            position: absolute;
            right: -9999px;
            top: -9999px;
        }
    }

    &__container {
        padding-top: $space * 11;

        @include breakpoint(tablet) {
            padding-top: 252px;
        }
    }

    &__title {
        line-height: $line-height-standard;
        margin-bottom: $space * 2;

        @include breakpoint(tablet) {
            margin-bottom: $space * 1.5;
        }

        &--light {
            color: $white;
        }
    }

    &__text {
        line-height: $line-height-increased;
        max-width: 487px;
        font-family: $font-heading;
        font-size: $font-size-increased;
        margin-bottom: $space * 2;

        @include breakpoint(tablet) {
            margin-bottom: $space * 3;
        }

        &--light {
            color: $white;
        }
    }

    &__button {
        @extend .button;
        @extend .button--primary;

        box-shadow: $shadow-standard;
    }
}
