@import '../settings/colors';

.header {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 4;
    background: none;
    transition: all 0.2s cubic-bezier(0.25, 0.1, 0.25, 1);

    &:after {
        position: absolute;
        display: block;
        content: '';
        left: 0;
        top: 0;
        background: linear-gradient($secondary 0%, transparent 100%);
        height: 100%;
        width: 100%;
        z-index: $z-background;
    }

    &.active {
        position: fixed;
        height: 100vh;
        background: $secondary;
    }
}
