/* COLOR DEFINITIONS */
$charcoal: #333333;
$pistachio: #00C08B;
$navyblue: #002855;
$lightblue: #8093AA;
$strongblue: #0055B8;

/* GREYSCALE */
$grey-light: #F9F9F9;
$grey-mid: #8093AA;
$white: #FFFFFF;
$black: #000000;

/* COLOR SWATCHES */
$primary: $pistachio;
$secondary: $navyblue;
$tertiary: $charcoal;

/* COLOR ASSIGNMENTS */
$color-background-primary: $white;
$color-text: $charcoal;
$color-link: $primary;
$color-hover: $secondary;
$color-placeholder: $grey-mid;
$color-border: $primary;
$color-disabled: $grey-light;
